.thumb {
    width: 100%;
    height: 255px;
    position: relative;
    margin-bottom: 20px;
    border-radius: 10px;
    animation: fadeInUp 600ms ease-in-out;
    overflow: hidden;
  
    &__img {
      width: inherit;
      height: inherit;
      object-fit: cover;
      border-radius: inherit;
      transform: scale(1);
      transition: transform 300ms;
    }
  
    &__name {
      position: absolute;
      bottom: 45px;
      font-size: 18px;
      font-weight: 500;
      margin: 15px 20px;
      color: $white;
      box-sizing: inherit;
    }

    &__nickname {
      position: absolute;
      bottom: 20px;
      font-size: 18px;
      font-weight: 500;
      margin: 15px 20px;
      color: $white;
      box-sizing: inherit;
    }

    &__bounty {
      position: absolute;
      bottom: -5px;
      font-size: 18px;
      font-weight: 500;
      margin: 15px 20px;
      color: $white;
      box-sizing: inherit;
    }
  
    &__overlay {
      position: absolute;
      top: 0;
      background: linear-gradient(transparent, rgba(0, 0, 0, 0.5));
      height: 100%;
      width: 100%;
      border-radius: inherit;
    }

    &:hover {
      > .thumb__img {
        transform: scale(1.05);
      }
    }
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(5px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }